.about{
    width: 100%;
    margin: 0px;
    padding-top: 10vh;
    background-image: url("../Images/about.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.hh{
    color: rgb(176, 176, 196);
    margin-top: 0px;
}

.container{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.container img{
    width:80%;
    margin-left: 10%;
}

.type p{
    font-size: 25px;
    padding-right: 20px;
    color:white;
}

@media all and (max-width:800px){
    .about{
            background-size: cover;
    }
    .container img{
        width:100%;
        margin-left: 10px;
    }
    .type p{
        font-size: 20px;
    }
}

@media all and (max-width:400px){
    
    .container{
        grid-template-columns: 1fr;
    }
    .type p{
        padding-right: 0px;
    }
}